

.ant-btn{
    width: 170px;
    height: 50px !important;
    font-size: 16px !important;
    border: none;
}

.ant-btn:nth-of-type(1){
    background-color: #00da00;
    color: black;
}

.ant-btn:nth-of-type(2){
    background-color: #1462e1;
}

.ant-btn:nth-of-type(3){
    background-color: #ff0004;
}