
.apt-modal{
    min-width: 60vw;
    
}

.ant-modal-header{
    border-radius: 20px;
    /* font-size: 38px; */
}

.ant-modal-title{
    font-size: 38px;
    text-transform: lowercase;
    text-align: center;
    font-weight: bolder;
}

.ant-modal-content{
    border-radius: 20px;
    padding: 20px;
}

.ant-input, .ant-picker{
    border-radius: 25px;
    height: 45px;
    box-shadow: inset -6px -6px 9px 0 #fff, inset 4px 4px 10px 0 #b3b3b3;
    /* height: 50px; */
}

.ant-btn{
    margin: 0px 10px;
    border-radius: 25px;
    height: 50px;
    width: 110px;
    box-shadow: -3px -3px 8px 2px #f5f5f5, 3px 3px 8px 2px #a3b1c6;
}

.ant-form-item-label{
    margin-left: 10px;
    /* color: #999998; */
}

.ant-form-item-label > label, .ant-btn{
    font-size: 18px;
}

img.appointment-details-image{
    margin-top: 15px;
    /* margin-left: 15px; */
    max-width: 100%;
    height: auto;
}